import Vue from 'vue'
import Router from 'vue-router'


import sie from '@/components/project/v_sie'
import yd from '@/components/project/v_yd'
import oya from '@/components/project/v_oya'


Vue.use(Router)

export default new Router({
    routes: [

        {
            path: '/',
            redirect: '/sie'
        },
        {
            path: '/sie',
            name: 'sie',
            component: sie
        },
        {
            path: '/yd',
            name: 'yd',
            component: yd
        },
        {
            path: '/oya',
            name: 'oya',
            component: oya
        }

    ]
})



