<template>
    <div id="app">
        <div v-if="hidden">
            <v-header :baseInfo="baseInfo"></v-header>

            <v-about></v-about>

            <v-experience></v-experience>

            <v-skill></v-skill>

            <v-project></v-project>

            <v-footer></v-footer>
        </div>


    </div>
</template>

<script>
    // vue相对路径不能省略，必须是以./ ../开头 ，绝对路径以/开头
    import header from './components/header/v_header.vue'
    import about from './components/content/v_about.vue'
    import nav from './components/menu/v_nav.vue'
    import experience from './components/content/v_experience.vue'
    import skill from './components/content/v_skill.vue'
    import project from './components/project/v_project.vue'
    import footer from './components/footer/v_footer.vue'

    import data from '@/data.json'
    export default {
        name: 'app',
        data:function () {
            return{
                baseInfo:{},
                hidden:true,
            }
        },

        //获取数据
        created: function () {
            this.baseInfo = data.baseInfo
        },

        // 组件命名不要与html标签冲突
        components: {
            'v-header': header,
            'v-about': about,
            'v-experience': experience,
            'v-skill': skill,
            'v-project': project,
            'v-footer': footer,
        }

    }
</script>

<style>

    #app {
        font-family: 'Avenir', Helvetica, Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        text-align: center;
        color: #2c3e50;
        width: 100%;
        position: relative;
        margin: 0;
        padding: 0;
    }
    .router-link-active{
        background: #41b883;
        color:#fff!important;
    }

</style>



